.friend_list{
    padding: 0;
}
.item {
    display: flex;
    align-items: center;
    border: 1px solid #ccc;
    margin-bottom: 15px;
    padding: 10px;
    padding-left: 15px;
    width: 250px;
    box-shadow: 3px 3px 6px 0 rgb(0 0 0 / 75%);
  }
  
  .item:last-child {
    margin-bottom: 0;
  }
  
  .status {
    display: block;
    width: 15px;
    height: 15px;
    margin-right: 20px;
    border-radius: 50%;
  }
  
  .status__true {
    composes: status;
    background-color: #1b9c26;
  }
  
  .status__false {
    composes: status;
    background-color: #a82929;
  }
  
  .avatar {
    
    margin-right: 20px;
    
  }
  
  .name {
    font-size: 20px;
    font-weight: 700;
  }
